import React, { useState } from "react";

export default function ReserveButton({ bookId, userId }) {
 const [loading, setLoading] = useState(false);
 const [reserved, setReserved] = useState(false);

 // La date de fin de réservation (par exemple, 7 jours à partir d'aujourd'hui)
 const reservedUntil = new Date();
 reservedUntil.setDate(reservedUntil.getDate() + 7); // Par exemple, réserver pendant 7 jours à partir d'aujourd'hui
 const formattedDate = reservedUntil.toISOString(); // Format ISO, ex: "2025-03-01T00:00:00Z"

 const handleReserve = async () => {
  setLoading(true); // Lancement du chargement pendant la réservation
  try {
   // Appel à l'API de réservation avec l'ID du livre, l'ID de l'utilisateur et la date de réservation
   const response = await fetch("http://localhost:5000/api/reservations", {
    method: "POST",
    headers: {
     "Content-Type": "application/json",
    },
    body: JSON.stringify({
     userId, // ID de l'utilisateur qui réserve
     bookId, // ID du livre à réserver
     reservedUntil: formattedDate, // Date jusqu'à laquelle la réservation est valable
    }),
   });

   const data = await response.json();
   console.log("Réponse de la réservation:", data); // Affichez la réponse pour le débogage

   if (data.success) {
    setReserved(true); // Si la réservation est réussie, on met à jour l'état
   } else {
    console.log("Réponse échouée:", data); // Affichez la réponse pour le débogage
    alert("La réservation a échoué.");
   }
  } catch (error) {
   console.error("Erreur lors de la réservation:", error);
   alert("Erreur de connexion, veuillez réessayer.");
  } finally {
   setLoading(false); // Fin du chargement, quelle que soit l'issue
  }
 };

 return (
  <button
   onClick={handleReserve}
   disabled={loading || reserved}
   className={`mt-2 py-2 px-4 rounded transform transition-transform duration-300 ease-in-out ${
    loading ? "bg-gray-500" : "bg-reservationButtonBg"
   } text-white hover:scale-105 hover:bg-survolButtonBg`}
  >
   {loading ? "Chargement..." : reserved ? "Réservé" : "Réserver"}
  </button>
 );
}
