import React from "react";
import ReserveButton from "@/components/pageweb/buttons/ReserveButton.jsx";
import FormDate from "@/components/pageweb/buttons/FormDateButton.jsx";

export default function BookList({ books }) {
 if (!books || books.length === 0) {
  return <p>Aucun livre disponible.</p>;
 }

 return (
  <div className="book-list">
   {books.map((book) => (
    <div
     key={book.id}
     className="book-card border p-4 mb-4 rounded shadow-md transform transition-transform duration-300 ease-in-out hover:scale-105 hover:shadow-xl"
    >
     <h2 className="text-xl font-bold">{book.title}</h2>
     <p>
      <strong>Auteur:</strong> {book.author}
     </p>
     <p>
      <strong>Description:</strong>{" "}
      {book.description || "Pas de description disponible"}
     </p>
     <p>
      <strong>Genre:</strong> {book.genre || "Genre non spécifié"}
     </p>
     <p>
      <strong>Disponible:</strong> {book.isAvailable ? "Oui" : "Non"}
     </p>
     {/* Utilisation du composant ReserveButton */}
     <FormDate />
    </div>
   ))}
  </div>
 );
}
